:root {
  --theme-color: #ff6033;
  --theme-color-rgb: 255, 96, 51;
}

html, body {
  margin: 0;
  padding: 0;
  position: relative;
}

body {
  color: #fff;
  background: #000;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

a {
  color: var(--theme-color);
}

h1 {
  font-size: 3em;
  line-height: 1.2;
}

h2 {
  margin: .75em 0 .5em;
  font-size: 2.5em;
  line-height: 1.25;
}

main {
  max-width: 1200px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 40px;
}

header, section, footer {
  margin: 40px 0;
}

header {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.avatar {
  width: 20%;
  height: auto;
  border: 4px solid #444;
  border-radius: 50%;
  margin-right: 40px;
}

.header-content {
  width: 100%;
  min-width: 0;
}

header h1 {
  margin: 0;
  line-height: 1.2;
}

.subtitle {
  margin: 1em 0;
}

.subtitle span.dot {
  opacity: .5;
  font-size: 14px;
}

.subtitle span[role="img"] {
  vertical-align: middle;
  font-size: 1.5em;
}

.contacts a {
  vertical-align: middle;
  background: #ffffff1a;
  border-radius: 8px;
  align-items: center;
  margin-bottom: 8px;
  margin-right: 8px;
  padding: 4px 8px;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  display: inline-flex;
}

.contacts a:hover {
  background: #ffffff20;
}

.contacts a:active {
  background: #ffffff13;
}

.contacts .icon {
  width: 24px;
  height: 24px;
  align-items: center;
  margin-right: 8px;
  display: flex;
}

.contacts img {
  max-width: 100%;
  max-height: 100%;
}

.location {
  align-items: center;
  margin-bottom: 8px;
  display: flex;
}

.branding {
  margin-top: -.25em;
  margin-bottom: 2em;
}

.widget {
  --preloader-color: var(--theme-color);
}

.widget + .widget {
  margin-top: 16px;
}

codersrank-summary {
  --bg-color: transparent;
  --badge-bg-color: #161616;
  --badge-text-color: #fff;
  --badges-padding: 0px;
  --badge-padding: 8px;
  --badge-margin: 16px;
  --badge-border-radius: 8px;
  --badge-rank-font-size: 16px;
  --badge-technology-font-size: 18px;
  --badge-location-font-size: 16px;
  --badge-icon-size: 32px;
  --badge-border: 1px solid #0000001a;
  --badge-box-shadow: none;
}

.activity-widget {
  background: #161616;
  border-radius: 8px;
  padding: 8px;
}

codersrank-activity {
  --bg-color-0: #111;
  --bg-color-1: rgba(var(--theme-color-rgb), .25);
  --bg-color-2: rgba(var(--theme-color-rgb), .5);
  --bg-color-3: rgba(var(--theme-color-rgb), .75);
  --bg-color-4: rgba(var(--theme-color-rgb), 1);
  --label-text-color: #ffffff8a;
  --svg-width: 1080px;
  justify-content: flex-end;
  margin-top: -100px;
  padding-top: 100px;
  display: flex;
  overflow: hidden;
}

codersrank-skills-chart {
  --label-text-color: #ffffff8a;
  --chart-bg-color: #161616;
  --chart-border-radius: 8px;
  --axis-bg-color: transparent;
  --label-font-size: 12px;
  overflow: hidden;
}

codersrank-portfolio, codersrank-work-experience, codersrank-education {
  --item-bg-color: #161616;
  --item-border-radius: 8px;
  --item-spacing: 16px;
  --item-padding: 16px;
  --logo-size: 64px;
  --tag-bg-color: #ffffff13;
}

codersrank-portfolio {
  --grid-columns: 2;
  --link-text-color: var(--theme-color);
  --link-bg-color: #ffffff1a;
  --link-hover-bg-color: #ffffff20;
  --link-active-bg-color: #ffffff13;
  --link-border-radius: 8px;
  --link-padding: 4px 8px;
  --link-text-transform: none;
  --link-font-weight: 500;
  --link-margin: 8px;
}

footer {
  text-align: center;
  opacity: .55;
}

@media (width <= 768px) {
  header {
    display: block;
  }

  .avatar {
    width: 160px;
    margin-bottom: 20px;
  }

  codersrank-portfolio, codersrank-work-exprerience, codersrank-education {
    --grid-columns: 1;
    --item-spacing: 8px;
    --logo-size: 48px;
  }
}

@media (width <= 640px) {
  main {
    padding-left: 20px;
    padding-right: 20px;
  }

  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.5em;
  }

  .widget + .widget {
    margin-top: 8px;
  }

  codersrank-summary {
    --badge-margin: 8px;
  }

  codersrank-activity {
    --svg-width: 640px;
  }
}

